import { formatNumber } from "../../../utils/FormattingUtils";
import { applyTransformations } from "../../calculation/Calculation";
import { TransformationTags } from "../../calculation/TransformationTags";
import { getMonstiumGainPerSecFromCreatures } from "../../exploration/Creatures";
import { isDungeonFloorCompletedThisRun } from "../../exploration/dungeons/Dungeons";
import { hasFlag } from "../../Flags";
import { PlayerContextState } from "../../PlayerContext";
import { getResourceAmount, grantResource, Resource } from "../../Resources";
import {
  ActionBase,
  ActionEffect,
  ActionSubcategory,
  DoActionArgs,
} from "../Action";

function getBaseMonstiumGain(state: PlayerContextState): number {
  if (isDungeonFloorCompletedThisRun(state, "darknessTemple3F")) {
    return 1650;
  }
  if (isDungeonFloorCompletedThisRun(state, "darknessTemple2F")) {
    return 1000;
  }
  if (isDungeonFloorCompletedThisRun(state, "darknessTemple1F")) {
    return 420;
  }
  if (
    isDungeonFloorCompletedThisRun(state, "underseaPalaceFloor") ||
    isDungeonFloorCompletedThisRun(state, "volcanicCaveFloor") ||
    isDungeonFloorCompletedThisRun(state, "cloudCastleFloor") ||
    isDungeonFloorCompletedThisRun(state, "hallsOfEarthFloor")
  ) {
    return 220;
  }
  if (isDungeonFloorCompletedThisRun(state, "thunderTowerPeak")) {
    return 130;
  }
  if (isDungeonFloorCompletedThisRun(state, "thunderTowerMiddle")) {
    return 96;
  }
  if (isDungeonFloorCompletedThisRun(state, "thunderTowerBottom")) {
    return 80;
  }
  if (isDungeonFloorCompletedThisRun(state, "livingForestGrove")) {
    return 50;
  }
  if (isDungeonFloorCompletedThisRun(state, "livingForestDepths")) {
    return 38;
  }
  if (isDungeonFloorCompletedThisRun(state, "livingForestEntrance")) {
    return 27;
  }
  if (isDungeonFloorCompletedThisRun(state, "schoolBasementB3")) {
    return 15;
  }
  if (isDungeonFloorCompletedThisRun(state, "schoolBasementB2")) {
    return 9;
  }
  return 5;
}

class UseTimePiecesMonstium extends ActionBase {
  constructor() {
    super();
  }

  isVisible(state: PlayerContextState): boolean {
    return (
      getResourceAmount(state, Resource.TimePieces) > 0 &&
      isDungeonFloorCompletedThisRun(state, "schoolBasementB1")
    );
  }

  getName(): string {
    return "Use Time Pieces (Monstium)";
  }

  getArea(): string {
    return "Campus";
  }

  getSubcategory(): ActionSubcategory | undefined {
    return ActionSubcategory["Use Time Pieces"];
  }

  getDisplayDescription(state: PlayerContextState): string {
    let creaturesText = "";
    if (hasFlag(state, "creatures_unlocked")) {
      creaturesText = " as well as on your Creatures";
    }
    return `Use your time pieces to get some Monstium, depending on your Exploration progress${creaturesText}.`;
  }

  protected getBaseActionEffects(): Record<string, ActionEffect> {
    return {
      timePiecesPerMobEquivalent: {
        value: 100,
        tags: [TransformationTags.TimePiecePerMobMonstium],
      },
      timePiecesPerCreaturesSec: {
        value: 5,
        tags: [TransformationTags.TimePiecePerCreaturesSec],
      },
    };
  }

  protected getMonstiumEarnedPerMob(state: PlayerContextState): number {
    return applyTransformations(
      [Resource.Monstium, TransformationTags.Loot],
      state,
      getBaseMonstiumGain(state),
    );
  }

  protected getMonstiumPerAction(state: PlayerContextState): number {
    const actionEffects = this.getActionEffects(state);
    const timePiecesPerMobEquivalent = actionEffects.timePiecesPerMobEquivalent;
    const timePiecesPerCreaturesSec = actionEffects.timePiecesPerCreaturesSec;
    const timePieceCost = 3000;
    const mobs = timePieceCost / timePiecesPerMobEquivalent;
    const creatureSecs = timePieceCost / timePiecesPerCreaturesSec;
    return (
      this.getMonstiumEarnedPerMob(state) * mobs +
      (hasFlag(state, "creatures_unlocked")
        ? getMonstiumGainPerSecFromCreatures(state) * creatureSecs
        : 0)
    );
  }

  getDisplayEffect(state: PlayerContextState): string {
    const monstiumEarned = this.getMonstiumPerAction(state);
    return `Instantly obtain ${formatNumber(monstiumEarned, {
      showDecimals: false,
    })}:monstium:`;
  }

  canAfford(state: PlayerContextState): boolean {
    return (
      super.canAfford(state) &&
      getResourceAmount(state, Resource.TimePieces) >= 3000
    );
  }

  getCost(state: PlayerContextState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return {
      resources: {
        "Time Pieces": 3000,
      },
      items: {},
    };
  }

  doAction(args: DoActionArgs, state: PlayerContextState): PlayerContextState {
    const monstiumEarned = this.getMonstiumPerAction(state);

    state = grantResource(
      Resource.TimePieces,
      -(this.getCost(state).resources["Time Pieces"] ?? 0),
    )(state);
    state = grantResource(Resource.Monstium, monstiumEarned)(state);

    return state;
  }
}

export default new UseTimePiecesMonstium();
